.work {
  width: 100%;
  height: 100vh;
  background-color: black;
  position: relative;
  h2 {
    color: white;
    font-family: sans-serif;
    font-size: 2em;
    height: 100px;
    padding: 0 30px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  .projects {
    display: flex;
    position: relative;
    width: 100%;
    height: calc(100vh - 100px);
    padding: 40px 0 0 0;
    overflow: hidden;
    .project {
      flex-grow: 1;
      max-width: 25%;
      height: 100%;
      animation-name: toStart;
      animation-duration: 1s;
      animation-fill-mode: forwards;
      position: relative;
      span {
        z-index: 2;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 40%;
        background: rgb(1, 1, 1);
        background: linear-gradient(
          0deg,
          rgba(1, 1, 1, 0) 0%,
          rgba(1, 1, 1, 1) 75%
        );
      }
      img {
        z-index: 1;
        position: relative;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: 0.3s;
      }
      .box {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 80px;
        z-index: 0;
        color: white;
        justify-content: center;
        background-color: transparent;
        transform: translateY(100vh);
        transition: 0.3s 0s, background-color 0.2s 0.2s;
        p {
          text-align: center;
          font-size: 1em;
          line-height: 2em;
        }
      }
    }
    .project:hover {
      .box {
        z-index: 3;
        transform: translateY(0);
        background-color: rgba(1, 1, 1, 0.5);
      }
    }
  }
}

@keyframes toStart {
  0% {
  }
  100% {
    transform: translateX(0);
  }
}
