* {
  margin: 0;
}

div.lel {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  color: white;
  text-align: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 30px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

div h1 {
  font-size: 1.3em;
  letter-spacing: 3px;
  text-transform: uppercase;
}

div p {
  font-size: 1.8em;
  line-height: 1.3em;
  text-transform: uppercase;
  font-weight: bold;
  font-family: sans-serif;
  letter-spacing: 2px;
  line-height: 1.4em;
}

div button {
  padding: 10px 12px;
  border: 1px solid white;
  background-color: transparent;
  color: white;
  border-radius: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}

div button:hover {
  background-color: white;
  color: black;
}

#christopherLis {
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  opacity: 0;
}

#oneWithAn {
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  opacity: 0;
}

#viewWorkBtn {
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  opacity: 0;
}

@media (min-width: 768px) {
  div.lel {
    gap: 80px;
  }
  div h1 {
    font-size: 1.4em;
  }
  div p {
    font-size: 2em;
  }
}

@media (min-width: 1024px) {
  div p {
    font-size: 3em;
  }
}
