.work {
  width: 100%;
  height: 100vh;
  background-color: black;
  position: relative;
}

.work h2 {
  color: white;
  font-family: sans-serif;
  font-size: 2em;
  height: 100px;
  padding: 0 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.work .projects {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  width: 100%;
  height: calc(100vh - 100px);
  padding: 40px 0 0 0;
  overflow: hidden;
}

.work .projects .project {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  max-width: 25%;
  height: 100%;
  -webkit-animation-name: toStart;
          animation-name: toStart;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  position: relative;
}

.work .projects .project span {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 40%;
  background: #010101;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(1, 1, 1, 0)), color-stop(75%, #010101));
  background: linear-gradient(0deg, rgba(1, 1, 1, 0) 0%, #010101 75%);
}

.work .projects .project img {
  z-index: 1;
  position: relative;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.work .projects .project .box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 80px;
  z-index: 0;
  color: white;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: transparent;
  -webkit-transform: translateY(100vh);
          transform: translateY(100vh);
  -webkit-transition: 0.3s 0s, background-color 0.2s 0.2s;
  transition: 0.3s 0s, background-color 0.2s 0.2s;
}

.work .projects .project .box p {
  text-align: center;
  font-size: 1em;
  line-height: 2em;
}

.work .projects .project:hover .box {
  z-index: 3;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  background-color: rgba(1, 1, 1, 0.5);
}

@-webkit-keyframes toStart {
  0% {
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes toStart {
  0% {
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
